.terminal-content {
  padding-bottom: 50px;
}

.terminal-banner {
    padding: 0 50px 0 50px;
    margin: 0;
    white-space: break-spaces;
    font-size: calc(1vw + 7px);
    font-weight: 900;
    color: whitesmoke;
    text-shadow: 0 0 5px whitesmoke;
}

.terminal-header {
    font-size: calc(0.3vw);
    font-weight: 900;
    color: whitesmoke;
    text-shadow: 0 0 5px whitesmoke;
}

.terminal-input {
  color: whitesmoke;
  text-shadow: 0 0 3px whitesmoke;
  font-size: calc(1vw + 7px);
  font-weight: 900;
  padding-right: 5px;
  font-family: inherit;
  font-size: inherit;
  background: transparent;
  border: 0px;
  outline: none; /* no highlight on focus */
  width: 100%;
}

.terminal-output {
  padding: 20px 50px 0 50px;
  color: whitesmoke;
  font-size: calc(1vw + 7px);
  font-weight: 900;
  color: whitesmoke;
  text-shadow: 0 0 3px whitesmoke;
}

.logo-container {
      display: flex;
      padding-left: 45px;
      align-items: flex-end;
}

.gitHub-logo {
    font-size: 4px;
    font-weight: 900;
    color: whitesmoke;
    text-shadow: 0 0 5px whitesmoke;

    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.gitHub-logo:hover {
    font-weight: 900;
    text-shadow: 0 0 15px #00ff00;
    cursor: pointer;
}

.linkedIn-logo {
  font-size: 6px;
    font-weight: 900;
    color: whitesmoke;
    text-shadow: 0 0 5px whitesmoke;  

    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.linkedIn-logo:hover {
    font-weight: 900;
    text-shadow: 0 0 15px #0072b1;  
    cursor: pointer;
}

.mail-logo {
  font-size: 7px;
    font-weight: 900;
    color: whitesmoke;
    text-shadow: 0 0 5px whitesmoke;

    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.mail-logo:hover {
    font-weight: 900;
    text-shadow: 0 0 15px #BB001B;
    cursor: pointer;
}

.subText-typing {
    font-size: calc(1vw + 7px);
    font-weight: 900;
    color: whitesmoke;
    text-shadow: 0 0 3px whitesmoke;
}

.explain-typing {
    font-size: calc(1vw + 7px);
    font-weight: 900;
    color: whitesmoke;
    text-shadow: 0 0 3px whitesmoke;
}

.input-area {
    padding: 20px 0 0 50px;
    display: flex;
    align-items: center;
    font-size: calc(1vw + 7px);
    font-weight: 900;
    text-shadow: 0 0 5px whitesmoke;
}

.input-prompt {
    color: greenyellow;
    font-size: 15px;
}

.resume-container {
  margin-left: 50px;
  margin-top: 20px;
  font-size: calc(1vw + 7px);
  font-weight: 900;
}

.resume-link {
  padding: 20px 20 0 50px;
  color: greenyellow;
  text-shadow: 0 0 3px greenyellow;
  text-decoration: none;
}
.resume-link:hover {
  color: rgb(201, 253, 123);
  text-shadow: 0 0 5px rgb(201, 253, 123);
  cursor: pointer;
  text-decoration: none;
}

.indent {
  margin-left: 1em;
}

.green-text {
  color: greenyellow;
  text-shadow: 0 0 5px greenyellow;
}

.yellow-text {
  color: yellow;
  text-shadow: 0 0 5px yellow;
}

.red-text {
  color: red;
  text-shadow: 0 0 5px red;
}

.subText-container {
  padding-left: 45px;
}